.Product_sec{
    width: 100%;    
    // background-color: red;
    >main{
        width: 100%;
        display: flex;
        justify-content: center;
        div{
              width: 24vw;
              position: absolute;
              top: -10rem;
              height: 8rem;
              margin-top: 1rem;
              border-radius: 1rem;
              background-color: rgb(223, 223, 255);
              display: flex;
              flex-direction: column;
              align-items: center;
              transition: all ease 0.5s;
              h3{
                text-align: center;
                margin-top: 1rem;
              }
              >button{
                margin: 2rem ;
                width: 4rem;
                padding: 0.5rem;
                cursor: pointer;
                height:2rem;
                border-radius: 5px;
                border: none;
                font-weight: 600;
                background-color: rgba(0,0,0,0.6);
                color: #fff;
              }


        }
    }
    >div{
        width: 100%;
        height: 90%;
        // background-color: blue;
        display: flex;
        justify-content:space-evenly;
        padding:2rem;

        .prod_img{
              width: 30vw;
              height: 25rem;
              margin: 2rem 0;
            //   background-color: pink;
              >img{
                  width: 100%;
                  height: 100%;
              }
        }
        .prod_desc{
                width: 40%;
                // background-color: red;
                margin-left: 3rem;
                h1{
                    margin: 2rem;

                }
                >p{
                    margin: 1rem 2rem;
                }
                >h3{
                    margin: 1rem 2rem;
                    display: inline;
                    h3{
                        display: inline;
                    }
                }
                >.quant_detail{
                    margin: 2rem;
                    >span{
                        border: 2px solid ;
                       padding: 0.5vw 2vw;
                       font-size: 1.2rem;
                       font-weight: 600;
                       cursor: pointer;    
                    //    border: none;
                    //    background-color: red;

                    }
                    
                    
                }
                .buy_detail{
                    margin: 2rem;
                    >input{
                        width: 14rem;
                        height: 2rem;
                        border: none;
                        outline: none;
                        margin: 0.5rem 0;
                        border-bottom: 2px solid ;
                    }
                }
                button{
                    margin: 0.4rem 2rem;
                    width: 5rem;
                    cursor: pointer;
                    height: 2rem;
                    border-radius: 5px;
                    border: none;
                    font-weight: 600;
                    background-color: rgba(0,0,0,0.6);
                    color: #fff;
                }
        }
    }

}